<template>
  <div id="app">
    <com-sidebar v-if="isHidden" :width="responsiveSidebarWidth"></com-sidebar>
    <div id="site-content" :style="bodyStyle">
      <topbar></topbar>
      <router-view></router-view>
      <Footer></Footer>
    </div>
  </div>
</template>

<script>
  import ComSidebar from "content/ComSidebar/ComSidebar";
  import topbar from "common/topbar/topbar";
  import Footer from "common/footer/Footer";

  export default {
    name:'App',
    components:{
      topbar,
      ComSidebar,
      Footer
    },
    data(){
      return {
        device_width:document.body.clientWidth || document.documentElement.clientWidth,
        timer: false
      }
    },
    watch:{
      /* （方法一）监听device_width的变化:一定要定义window.onresize事件才能监听device_width
      *          不然是监听不到device_width的值变化的
      *          这里在监听方法中定义了一个定时器，用以防止页面监听频繁过于抖动 */
      /* （方法二）通过window.addEventListener方法监听device_width */
      device_width(val){
        if (!this.timer) {
          this.device_width = val
          this.timer = true
          let that = this
          setTimeout(function () {
            that.timer = false
          }, 400);

          // 当device_width改变时，发出事件并传递出去这个值
          this.$bus.$emit('browserChange',this.device_width);
        }
      }
    },
    mounted() {
      // 监听浏览器视口宽度并将值赋给 this.device_width
      window.onresize = () => {
        return (() => {
          this.device_width = document.body.clientWidth;
        })()
      }
    },
    computed:{
      /* 用计算属性设置窗口变化的响应值，而不要在data中写死它，
         否则一旦浏览器刷新时将使用data中的值而不是重新计算 */
      isHidden(){
        return this.device_width > 568;
      },

      /* siteContent的响应式style */
      bodyStyle(){
        let styleObj = {};
        styleObj.flex = 'auto';
        styleObj.position = 'absolute';
        styleObj.right = '0px';
        styleObj.bottom = '0px';
        if(this.device_width <= 568){
          styleObj.width = '100%';
          styleObj.height = '100%';
          styleObj.top = '44px';
          styleObj.left = '0px';
        }else{
          if(this.device_width > 568 && this.device_width <= 768){
            styleObj.top = '56px';
            styleObj.left = '60px';
          }
          else if(this.device_width > 768 && this.device_width <= 1200){
            styleObj.top = '56px';
            styleObj.left = '70px';
          }
          else if(this.device_width > 1200){
            styleObj.top = '56px';
            styleObj.left = '80px';
          }
        }

        return styleObj;
      },

      /* 根据浏览器宽度动态决定sidebar的宽度 */
      responsiveSidebarWidth(){
        if(this.device_width <= 768){
          return 60;
        }else if(this.device_width > 768 && this.device_width <= 1200){
          return 70;
        }else {
          return 80;
        }
      }
    }
  }
</script>

<style lang="less">
  @import "assets/less/base";

  #app{
    margin: 0;
    padding: 0;
    height: 100%;
    display: flex;
    background-color: @color-lightwhite;
    position: relative;
    #site-content{

    }
  }
</style>
