const Home = () => import("views/home/Home");
const Blog = () => import("views/blog/Blog");
const Collection = () => import("views/collection/Collection");
const About = () => import("views/aboutme/About");
const Contact = () => import("views/contact/Contact");
const Detail = () => import("views/detail/Detail");
// const Login = () => import("views/account/Login");
// const Register = () => import("views/account/Register");
// const BlogOutline = () => import("views/blogs/Outline");
