<template>
  <sidebar :width="width">
    <div>
      <nav-button v-for="item in nav_data" :key="item.name" :path="item.path">
        <template #icon>
          <i :class="item.icon"></i>
        </template>
        <template #name>
          {{item.name}}
        </template>
      </nav-button>
    </div>
  </sidebar>
</template>

<script>
  /* 封装项目中使用的sidebar组件 */

  import sidebar from "common/sidebar/sidebar";
  import NavButton from "common/widgets/NavButton";

  export default {
    name: "ComSidebar",
    components:{
      sidebar,
      NavButton
    },
    props:{
      width:Number
    },
    data(){
      return{
        nav_data:[
          {icon:'fas fa-home',name:'首页',path:'/home'},
          {icon:'fas fa-blog',name:'博客',path:'/blogs'},
          {icon:'fas fa-folder-plus',name:'归档',path:'/collections'},
          {icon:'fas fa-user',name:'关于我',path:'/about'},
          {icon:'fas fa-comments',name:'留言板',path:'/contact'},
        ]
      }
    },
  }
</script>

