<template>
  <button class="normal-btn" :style="ButtonStyle"
          @mouseenter="btnEnter" @mouseleave="btnLeave">{{value}}</button>
</template>

<script>
  export default {
    name: "NorButton",
    props:{
      bgColor:{
        type:String,
        default:'#87d068'
      },
      txtColor:{
        type:String,
        default:'#ffffff'
      },
      activeBgColor:{
        type:String,
        default:'#66AB0D'
      },
      activeTxtColor:{
        type:String,
        default:'#ffffff'
      },
      value:String,
      height:{
        type:Number,
        default:40
      },
      width:{
        type:Number,
        default:80
      },
      border:{
        type:String,
        default:'#dcdee2'
      },
      activeBorder:{
        type:String,
        default:'#66AB0D'
      },
      type:{
        type:String,
        default:'fill'
      }
    },
    data(){
      return{}
    },
    computed:{
      ButtonStyle(){
        let ButtonStyle = {};
        ButtonStyle.width = this.width;
        ButtonStyle.height = this.height;
        if(this.type === 'fill'){
          ButtonStyle.backgroundColor = this.bgColor;
          ButtonStyle.color = this.txtColor;
        }else if(this.type === 'hollow'){
          ButtonStyle.backgroundColor = 'transparent';
          ButtonStyle.color = '#444444';
          ButtonStyle.borderColor = this.border;
          ButtonStyle.borderStyle = 'solid';
          ButtonStyle.borderWidth = '1px';
        }
        return ButtonStyle;
      },

    },
    methods:{
      btnEnter(e){
        if(this.type === 'hollow'){
          e.target.style.borderColor = this.activeBorder;
        }
        else if(this.type === 'fill'){
          e.target.style.backgroundColor = this.activeBgColor;
          e.target.style.color = this.activeTxtColor;
        }
      },
      btnLeave(e){
        if(this.type === 'hollow'){
          e.target.style.borderColor = this.border;
        }
        else if(this.type === 'fill'){
          e.target.style.backgroundColor = this.bgColor;
          e.target.style.color = this.txtColor;
        }
      }
    }
  }
</script>

<style scoped lang="less">
  .normal-btn{
    padding: 5px 20px;
    border-radius: 8px;
    text-align: center;
    cursor: pointer;
    margin: 0 5px;
  }
</style>