<template>
  <div id="top-bar">
    <div class="left">
      <span class="site-name">杨豪的个人网站</span>
    </div>
    <div class="right">
      <Button type="primary" :size="btnSize" style="padding: 0 20px;margin-right: 10px;">登陆</Button>
      <Button :size="btnSize" style="padding: 0 20px">注册</Button>
    </div>
  </div>
</template>

<script>
  import NorButton from "../widgets/NorButton";

  export default {
    name: "topbar",
    components:{
      NorButton
    },
    data(){
      return{
        device_width:document.body.clientWidth,
      }
    },
    mounted() {
      this.$bus.$on('browserChange',(val) => {
        this.device_width = val;
      })
    },
    computed:{
      btnSize(){
        return this.device_width <= 568 ? 'small' : 'default';
      }
    }
  }
</script>

<style scoped lang="less">
  #top-bar{
    height: 56px;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: @color-white;
    padding: 0 10px;
    border-bottom: @color-border solid 1px;
    box-shadow: 2px 0 10px rgba(80,80,80,0.3);

    position: fixed;
    top: 0;left: 80px;right: 0;
    @media screen{
      @media(max-width: 568px){
        left: 0;
        height: 44px;
      }
      @media(min-width: 568px){
        left: 60px;
      }
      @media(min-width: 769px){
        left: 70px;
      }
      @media(min-width: 1200px){
        left: 80px;
      }
    }

    .left{
      .site-name{
        font-size: 24px;font-weight: 600;font-family: 华文楷体;
        letter-spacing: 1px;
        @media screen{
          @media(max-width: 568px){
            font-size: 16px;
          }
        }
      }
    }
  }
</style>