<template>
  <div class="nav-button" @click="toPage">
    <button>
      <slot name="icon"></slot>
    </button>
    <span class="name">
      <slot name="name">首页</slot>
    </span>
  </div>
</template>

<script>
  export default {
    name: "NavButton",
    props:{
      path:String
    },
    methods:{
      toPage(){
        this.$router.push(this.path);
      }
    }
  }
</script>

<style scoped lang="less">
  .nav-button{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px 0;
    &:hover{
      button{
        border: @color-doulv solid 1px;
        box-shadow: none;
        background-color: @color-doulv;
        i{
          color: @color-white;
        }
      }
      .name{
        color: @color-doulv;
      }
    }
    button{
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background: #ffffff;
      box-shadow: 0 0 10px rgba(190,190,200,0.7);
      cursor: pointer;
      i{
        font-size: 16px;
        color: #444444;
      }
      @media screen{
        @media(max-width: 768px){
          width: 40px;
          height: 40px;
          i{
            font-size: 14px;
          }
        }
        @media(min-width: 769px){
          width: 45px;
          height: 45px;
        }
        @media(min-width: 1200px){
          width: 50px;
          height: 50px;
        }
      }
    }
    .name{
      padding-top: 8px;
      font-size: 14px;
      letter-spacing: 2px;
      cursor: pointer;
      @media screen{
        @media (max-width: 768px){
          font-size: 12px;
        }
      }
    }
  }
</style>